import {Component} from 'react'
import '../styles/profile.css'
import * as NasaAPI from '../utils/NasaAPI'
import * as ux from '../utils/uxFunctions'
class Profile extends Component{
    state = {
        user: this.props.user,
        imgs: []
    }
    componentDidMount(){
        ux.startLoading("profile-posts");     
        NasaAPI.getApod()
                .then(res => {
                    const data = res.map((item) => {
                        const post = {
                            user: "NasaAPOD",
                            url: item.url,
                            date: item.date,
                            desc: item.explanation,
                            type: item.media_type
                        }
                        return post
                    })
                    return data
                })
                .then(data => this.setState({
                    imgs: data
                })).then(() => {
                    const loading = document.getElementById("loading")
                    if(loading){
                        loading.remove();            
                    }
                })    
    }
    render(){
        const arr = this.state.imgs
        const {user} = this.state
        return(
            <div className="profile-body">
                <div className="profile-header">
                    <div className="profile-picture" style={{backgroundImage: `url(${user.displayPic})`}}></div>
                    <div className="profile-info">
                        <h1>{user.username}</h1>
                        <h2>{user.description}</h2>                    
                    </div>                    
                </div>
                <h3 className="profile-posts-title" id="profile-posts">Posts</h3>
                <div className="profile-posts" >
                    {arr.map((item) => {
                        return(
                            <div className="profile-post-item" style={{backgroundImage: `url(${item.url})`}}></div>
                        )
                    })}
                </div>
            </div>
        )}
}

export default Profile